const ChatList = ({ messages }) => {
  return (
    <ul>
      <style jsx>{`
        ul {
          height: 60%;
        }
      `}</style>
      {messages.map(msg => {
        return (
          <li>
            {msg.author} : {msg.text}
          </li>
        );
      })}
    </ul>
  );
};

export default ChatList;
